<template>
  <div class="goods-item" @click="itemClick">
      <img :src="showImage" alt="" @load="imageLoad">
      <div class="goods-info">
          <p>{{goodsItem.title}}</p>
          <span class="price">{{goodsItem.price}}  </span>
          <span class="collect">  收藏:{{goodsItem.cfav}}</span>
      </div>
  </div>
</template>

<script>
export default {
    props:{
        goodsItem:{
            type:Object,
            default(){
                return {}
            }
        }
    },
    computed:{
        showImage(){
            return this.goodsItem.image || this.goodsItem.show.img
        }
    },
    methods:{
        imageLoad(){
            // 事件总线
            if(this.$route.path.indexOf('/home') != -1){
                // console.log(this.$route.path.indexOf('/home')+'---');
                this.$bus.$emit("homeItemImageLoad")
            }else if(this.$route.path.indexOf('/detail') != -1){
                // console.log(this.$route.path.indexOf('/detail')+'===');
                this.$bus.$emit("detailItemImageLoad")
            }
        },
        itemClick(){
            // console.log("----");
            this.$router.push('/detail/'+this.goodsItem.iid)
        }
    }
}
</script>

<style scoped>
    .goods-item{
        padding-bottom: 45px;
        position: relative;
        width: 48%;
    }
    .goods-item img{
        width: 100%;
        /* 圆角边框 */
        border-radius: 10px;
    }
    .goods-info{
        font-size: 12px;
        position: absolute;
        bottom: 5px;
        right: 0;
        left: 0;
        overflow: hidden;
        text-align: center;
    }
    .goods-info p{
        /* overflow属性指定如果内容溢出一个元素的框，会发生什么。 */
        overflow: hidden;
        /* 规定当文本溢出包含它的元素，应该发生什么  此处是...处理表示 */
        text-overflow: ellipsis;
        /* whiteSpace 属性设置或返回如何处理文本中的制表符、换行符和空格符。nowrap忽略空格符，但是不允许换行符。 */
        white-space: nowrap;
        margin-bottom: 3px;
    }

    .goods-info .price{
        color: var(--color-high-text);;
        margin-right: 20px;
    }
    .goods-info .collect{
        position: relative;
    }
    .goods-info .collect::before{
        content: '';
        position: absolute;
        left: -15px;
        top: -1px;
        width: 14px;
        height: 14px;
        background: url("~assets/img/common/collect.svg") 0 0/14px 14px;
    }
</style>